body {
  font-family: 'Raleway', sans-serif;
}
.tableWrapper {
    /* The image used */
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0.2)), to(rgba(255,255,255,0.2))), url('./Images/road.jpg');
    background: -webkit-linear-gradient(rgba(255,255,255,0.2), rgba(255,255,255,0.2)), url('./Images/road.jpg');
    background: -o-linear-gradient(rgba(255,255,255,0.2), rgba(255,255,255,0.2)), url('./Images/road.jpg');
    background: linear-gradient(rgba(255,255,255,0.2), rgba(255,255,255,0.2)), url('./Images/road.jpg');

    /* Full height */
    height: 100%; 

    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.CustomZonesButtonWrapper {
  text-align: center;

}

.CustomZonesWrapper {
  text-align: center;
}


